import React, { useState, useEffect } from "react";
import { Pagination } from "antd";
import productApi from "../api/productApi";
import Search from "../Components/Search";
import UD from "../Components/Admin/Product/UD";

const Manage_product = () => {
  //state
  var [pagination, setpagination] = useState({
    pageIndex: 1,
  });
  var [filters, setfilters] = useState({
    pageIndex: 1,
    pageSize: 6,
    keyword: "",
  });
  const [loading, setloading] = useState(false);
  const handlePageChange = (page, pageSize) => {
    setloading(false);
    setfilters({
      ...filters,
      pageIndex: page,
      pageSize: pageSize,
    });
  };
  const [data, setData] = useState([]);
  useEffect(() => {
    const featchApi = async () => {
      const response = await productApi.getAll(filters);
      setData(response.items);
      setpagination({
        pageIndex: response.pageIndex,
        pageSize: response.pageSize,
        totalRows: response.toTalRecords,
      });
      setloading(true);
    };
    featchApi();
  }, [filters]);
  const handleFiltersChange = (newFilters) => {
    setloading(false);
    setfilters({
      ...filters,
      keyword: newFilters,
    });
  };
  return (
    <>
      <Search onSubmit={handleFiltersChange} />
      <UD loading={loading} Data={data} />
      <Pagination
        current={pagination.pageIndex}
        defaultPageSize={6}
        onChange={(page, pageSize) => handlePageChange(page, pageSize)}
        total={pagination.totalRows}
        showSizeChanger
        pageSizeOptions={[6, 10, 20, 30]}
      />
    </>
  );
};

export default Manage_product;
