import React, { useState } from "react";
import { Button, Table, Input, InputNumber, Form, notification,Spin } from "antd";
import productApi from "../../../api/productApi";
const EditableCell = ({
  editing,
  dataIndex,
  title,
  inputType,
  record,
  index,
  children,
  ...restProps
}) => {
  const inputNode = inputType === "number" ? <InputNumber /> : <Input />;
  return (
    <td {...restProps}>
      {editing ? (
        <Form.Item
          name={dataIndex}
          rules={[
            {
              required: true,
              message: `Please Input ${title}!`,
            },
          ]}
        >
          {inputNode}
        </Form.Item>
      ) : (
        children
      )}
    </td>
  );
};

const EditableTable = ({ loading,Data }) => {
  const [form] = Form.useForm();
  const [editingid, setEditingid] = useState("");

  const isEditing = (record) => record.id === editingid;
  const edit = (record) => {
    form.setFieldsValue({
      categoryName: "",
      lastName: "",
      email: "",
      Dob: "",
      phoneNumber: 0,
      ...record,
    });
    setEditingid(record.id);
  };
  const cancel = () => {
    setEditingid("");
  };
  const save = async (id) => {
    openNotificationWithIcon("warning","Từ, sẽ cập nhật lại sớm")
    // try {
    //   const row = await form.validateFields();
    //   await productApi
    //     .update({
    //       userId: localStorage.getItem("Id"),
    //       id: id,
    //       dob: row.dob,
    //       email: row.email,
    //       firstname: row.firstName,
    //       lastname: row.lastName,
    //       phone: row.phoneNumber,
    //     })
    //     .then(() => {
    //       setEditingid("");
    //       openNotificationWithIcon("success", "Update thành Công");
    //     })
    //     .catch((err) => {
    //       console.log(err);
    //     });
    // } catch (errInfo) {
    //   console.log("Validate Failed:", errInfo);
    // }
  };
  const openNotificationWithIcon = (type, mes) => {
    notification[type]({
      message: "Notification Title",
      description: mes,
    });
  };
  const columns = [
    {
      title: "Tên Sản Phẩm",
      dataIndex: "name",
      key: "name",
      editable: true,
      width: "15%",
    },
    {
      title: "description",
      dataIndex: "description",
      key: "description",
      editable: true,
      width: "15%",
    },
    {
      title: "categoryName",
      dataIndex: "categoryName",
      key: "categoryName",
      editable: true,
      width: "10%",
    },
    {
      title: "image",
      dataIndex: "image",
      render:(src)=><img src={"http://huylong37-001-site1.btempurl.com//user-content/"+src} style={{height:"50px",width:"100%"}} alt={src} />,
      editable: false,
      width: "10%",
    },
    {
      title: "Giá Bán",
      dataIndex: "price",
      key: "price",
      editable: true,
      width: "20%",
    },
    {
      title: "operation",
      dataIndex: "operation",
      width: "28%",
      colspan: 2,
      render: (_, record) => {
        const editable = isEditing(record);
        return editable ? (
          <>
            <Button
              onClick={() => save(record.id)}
              type="primary"
              danger
              shape="round"
            >
              Save
            </Button>
            <Button type="primary" danger shape="round" onClick={cancel}>
              Cancel
            </Button>
          </>
        ) : (
          <Button
            disabled={editingid !== ""}
            type="primary"
            shape="round"
            onClick={() => edit(record)}
          >
            Edit
          </Button>
        );
      },
    },
    {
      title: "Delete",
      render: (_, record) => (
        <Button
          onClick={() => {
            productApi.delete({"userId":localStorage.getItem("Id"), "id":record.id});
          }}
        >
          Delete
        </Button>
      )
      },
  ];

  const mergedColumns = columns.map((col) => {
    if (!col.editable) {
      return col;
    }
    return {
      ...col,
      onCell: (record) => ({
        record,
        dataIndex: col.dataIndex,
        title: col.title,
        editing: isEditing(record),
      }),
    };
  });
  console.log(loading)
  if(loading===true){
    return (
        <Form form={form} component={false}>
          <Table
            components={{
              body: {
                cell: EditableCell,
              },
            }}
            bordered
            rowKey="uid"
            dataSource={Data}
            columns={mergedColumns}
            rowClassName="editable-row"
            pagination={false}
          />
        </Form>
      );
  }
  return (
    <Spin
      style={{ display: "flex", justifyContent: "center" }}
      tip="Loading..."
      size="large"
    />
  );
};

export default EditableTable;
